.loader:before, .loader:after {
  content: "";
  -webkit-filter: drop-shadow(0 0 1rem rgba(255, 255, 255, .75));
  filter: drop-shadow(0 0 1rem rgba(255, 255, 255, .75));
  border-radius: 50%;
  animation: pulsOut 1.8s ease-in-out infinite;
  position: absolute;
}

.loader:before {
  width: 100%;
  padding-bottom: 100%;
  animation-name: pulsIn;
  box-shadow: inset 0 0 0 1rem #ff6b08;
}

.loader:after {
  width: calc(100% - 2rem);
  padding-bottom: calc(100% - 2rem);
  box-shadow: 0 0 #ff6b08;
}

@keyframes pulsIn {
  0% {
    opacity: 1;
    box-shadow: inset 0 0 0 1rem #ff6b08;
  }

  50%, 100% {
    opacity: 0;
    box-shadow: inset 0 0 #ff6b08;
  }
}

@keyframes pulsOut {
  0%, 50% {
    opacity: 0;
    box-shadow: 0 0 #ff6b08;
  }

  100% {
    opacity: 1;
    box-shadow: 0 0 0 1rem #ff6b08;
  }
}

